body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 500;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Rosatom", sans-serif;
  background-color: #ffffff;
  color: #333333;
  font-size: 20px;
  line-height: 1.3;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}
@media only screen and (max-width: 1279px) {
  body {
    font-size: calc(18px + (20 - 18) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  body {
    font-size: calc(16px + (18 - 16) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  body {
    font-size: calc(14px + (16 - 14) * (100vw - 0px) / (768 - 0));
  }
}

main {
  position: relative;
  z-index: 1;
}

section {
  transition: opacity 1s ease;
}

a {
  color: #333333;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Swiper */
.swiper-nav {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  gap: 8px;
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  width: 31px;
  height: 31px;
  cursor: pointer;
}
.swiper-nav .swiper-btn-prev:hover svg,
.swiper-nav .swiper-btn-next:hover svg {
  color: rgba(1, 95, 161, 0.7);
}
.swiper-nav .swiper-btn-prev.swiper-button-disabled svg,
.swiper-nav .swiper-btn-next.swiper-button-disabled svg {
  filter: brightness(0.1);
  opacity: 0.5;
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  color: #015fa1;
  transition: 0.3s ease;
}

/* Blocks */
.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}
.container--big {
  max-width: 1380px;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 218px;
  width: 100%;
  padding: 12px;
  border-radius: 22px;
  background-color: #015fa1;
  color: #ffffff;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  text-align: center;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover {
  background-color: rgba(1, 95, 161, 0.7);
}

.title {
  font-weight: 700;
  font-size: 36px;
  line-height: 1.3;
}

.title-gradient {
  position: relative;
  border-radius: 10px;
  padding: 16px;
  font-weight: 700;
  color: #ffffff;
  font-size: 40px;
  text-align: center;
  background: linear-gradient(45deg, #14256d 0%, #4494cf 55%, #4494cf 100%);
  background-size: 300%;
  animation: gradient-button 10s linear infinite;
}
@media only screen and (max-width: 1279px) {
  .title-gradient {
    font-size: calc(34px + (40 - 34) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title-gradient {
    font-size: calc(30px + (34 - 30) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title-gradient {
    font-size: calc(26px + (30 - 26) * (100vw - 0px) / (768 - 0));
  }
}

.counter {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 46px 40px;
  max-width: 400px;
}
@media only screen and (max-width: 991px) {
  .counter {
    max-width: 100%;
  }
}
.counter__item {
  overflow: hidden;
}
@media only screen and (max-width: 991px) {
  .counter__item {
    text-align: center;
  }
}
.counter__item:nth-child(3n+1) {
  grid-column: 1/span 2;
}
.counter__item > p {
  font-size: 18px;
  border-top: 1px solid #333333;
  padding-top: 8px;
}
@media only screen and (max-width: 767px) {
  .counter__item > p {
    font-size: calc(16px + (18 - 16) * (100vw - 0px) / (768 - 0));
  }
}
.counter__item-top {
  font-size: 25px;
  line-height: 1.25;
  overflow: hidden;
}
@media only screen and (max-width: 1279px) {
  .counter__item-top {
    font-size: calc(24px + (25 - 24) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .counter__item-top {
    font-size: calc(22px + (24 - 22) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .counter__item-top {
    font-size: calc(20px + (22 - 20) * (100vw - 0px) / (768 - 0));
  }
}
.counter__item-top p {
  transform: translateY(80px);
  transition: 0.5s ease;
}
.counter__item-top p.show {
  transform: translateY(0);
}
.counter__item-top span {
  font-size: 65px;
  line-height: 1;
}
@media only screen and (max-width: 991px) {
  .counter__item-top span {
    font-size: calc(55px + (65 - 55) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .counter__item-top span {
    font-size: calc(40px + (55 - 40) * (100vw - 0px) / (768 - 0));
  }
}
.counter__item-top span.odometer.odometer-theme-default {
  font-family: "Rosatom", sans-serif;
  line-height: 1;
  vertical-align: bottom;
}
.counter__item-top span.odometer.odometer-theme-default .odometer-digit {
  vertical-align: bottom;
}
.counter__item-top span.odometer.odometer-theme-default .odometer-digit .odometer-digit-spacer {
  vertical-align: bottom;
}

.circle {
  position: relative;
  max-width: 546px;
  width: 100%;
}
.circle::before {
  position: absolute;
  top: 14%;
  right: 13%;
  z-index: 8;
  content: "";
  min-width: 15px;
  max-width: 15px;
  height: 15px;
  background-color: #015fa1;
  border-radius: 50%;
}
.circle__inner {
  position: relative;
  height: 0;
  padding-top: 100%;
  border-radius: 50%;
  overflow: hidden;
}
.circle__item {
  position: absolute;
  background-color: #ffffff;
  border-radius: 50%;
  border: 1px solid #6dace4;
  transform: scale(1);
  transform-origin: top right;
  transition: 0.5s linear;
}
.circle__item.hidden {
  transform: scale(0);
}
.circle__item--colored {
  background-color: rgba(109, 172, 228, 0.2);
}
.circle__item--1 {
  z-index: 5;
  top: 11%;
  right: 11%;
  width: 24%;
  height: 24%;
}
.circle__item--1 > span {
  top: 39%;
  left: -4%;
}
@media only screen and (max-width: 575px) {
  .circle__item--1 > span {
    top: calc(50% - 10px);
    left: -5px;
  }
}
.circle__item--2 {
  z-index: 4;
  top: 8%;
  right: 8%;
  width: 45%;
  height: 45%;
}
.circle__item--2 > span {
  bottom: 5%;
  left: 11%;
}
@media only screen and (max-width: 575px) {
  .circle__item--2 > span {
    top: calc(50% - 10px);
    left: -5px;
  }
}
.circle__item--3 {
  z-index: 3;
  top: 5%;
  right: 5%;
  width: 67%;
  height: 67%;
}
.circle__item--3 > span {
  top: 45%;
  left: -1.5%;
}
@media only screen and (max-width: 575px) {
  .circle__item--3 > span {
    top: calc(50% - 10px);
    left: -5px;
  }
}
.circle__item--4 {
  z-index: 2;
  top: 2%;
  right: 2.5%;
  width: 84%;
  height: 84%;
}
.circle__item--4 > span {
  bottom: 1.3%;
  left: 23%;
}
@media only screen and (max-width: 575px) {
  .circle__item--4 > span {
    top: calc(50% - 10px);
    left: -5px;
  }
}
.circle__item--5 {
  z-index: 1;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.circle__item--5 > span {
  bottom: 27%;
  left: 3.4%;
}
@media only screen and (max-width: 575px) {
  .circle__item--5 > span {
    top: calc(50% - 10px);
    left: 0;
  }
}
.circle__item > span {
  position: absolute;
  display: flex;
  gap: 10px;
  max-width: 160px;
}
.circle__item > span::before {
  content: "";
  min-width: 10px;
  max-width: 10px;
  height: 10px;
  background-color: #015fa1;
  border-radius: 50%;
  filter: blur(2px);
  margin-top: 4px;
}
.circle__item > span span {
  font-size: 16px;
  line-height: 1.125;
}
@media only screen and (max-width: 767px) {
  .circle__item > span span {
    font-size: calc(10px + (16 - 10) * (100vw - 0px) / (768 - 0));
  }
}
@media only screen and (max-width: 575px) {
  .circle__item > span span {
    display: block;
    width: 142px;
    height: 60px;
    transform: translate(-38px, 54px) rotate(45deg);
  }
}

.news-card {
  position: relative;
  display: flex;
  min-height: 320px;
}
.news-card:hover .news-card__image {
  transform: rotate(-6deg);
}
.news-card:hover .news-card__content {
  background-color: #015fa1;
  transform: translate(8px, 110px) rotate(6deg);
}
.news-card:hover .news-card__text, .news-card:hover .news-card__button {
  color: #ffffff;
}
.news-card__link {
  position: absolute;
  inset: 0;
  z-index: 2;
}
.news-card__image, .news-card__content {
  border-radius: 30px;
  overflow: hidden;
  transition: 0.3s ease;
}
.news-card__image {
  position: absolute;
  inset: 1px;
}
.news-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.news-card__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  background-color: #e8f3ff;
  padding: 21px 20px 35px;
}
.news-card__text, .news-card__button {
  transition: 0.3s ease;
}
.news-card__text {
  font-size: 18px;
  line-height: 1.27;
}
.news-card__button {
  font-weight: 700;
  font-size: 18px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  padding: 58px 0;
  overflow: hidden;
  transition: 0.5s ease;
}
@media only screen and (max-width: 767px) {
  .header {
    padding: 30px 0;
  }
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header__logo, .header__logo-cn {
  transition: 0.3s ease;
}
.header__logo:hover, .header__logo-cn:hover {
  opacity: 0.7;
}

.footer {
