.carousel {
  padding: 63px 0 81px;
}
.carousel__wrapper {
  display: flex;
  align-items: flex-start;
  margin-top: 65px;
  margin-right: -25px;
}
@media only screen and (max-width: 1439px) {
  .carousel__wrapper {
    margin-right: 0;
  }
}
@media only screen and (max-width: 991px) {
  .carousel__wrapper {
    flex-direction: column;
    gap: 20px;
  }
}
.carousel__content {
  position: relative;
}
@media only screen and (max-width: 1439px) {
  .carousel__content {
    width: calc(100% - 481px - 90px);
    padding-right: 15px;
  }
}
@media only screen and (max-width: 991px) {
  .carousel__content {
    order: 1;
    width: 100%;
    padding-right: 0;
  }
}
.carousel__content-item {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}
.carousel__content-item.active {
  position: relative;
  opacity: 1;
  pointer-events: auto;
  transition: 0.3s ease;
}
.carousel__title {
  margin-right: -45px;
}
@media only screen and (max-width: 1439px) {
  .carousel__title {
    margin-right: 0;
  }
}
.carousel__text {
  max-width: 407px;
  margin-top: 41px;
}
.carousel__button {
  margin-top: 43px;
}
.carousel__slider {
  position: relative;
  max-width: 481px;
  width: 100%;
  margin-top: 140px;
  aspect-ratio: 15/8;
}
@media only screen and (max-width: 991px) {
  .carousel__slider {
    left: -40px;
    margin: 50px auto 0;
  }
}
@media only screen and (max-width: 767px) {
  .carousel__slider {
    max-width: 300px;
  }
}
.carousel__slide {
  position: absolute;
  inset: 0;
  border-radius: 40px;
  overflow: hidden;
  cursor: pointer;
  transition: 0.3s ease;
}
.carousel__slide.next::after {
  position: absolute;
  inset: 0;
  content: "";
  background-color: #6dace4;
}
.carousel__slide.last::after {
  position: absolute;
  inset: 0;
  content: "";
  background-color: #015fa1;
}
.carousel__slide:nth-child(1) {
  z-index: 3;
  transform: translate3d(0, 0, -10px);
}
.carousel__slide:nth-child(2) {
  z-index: 2;
  transform: translate3d(40px, -40px, -10px);
}
.carousel__slide:nth-child(3) {
  z-index: 1;
  transform: translate3d(80px, -80px, -10px);
}
.carousel__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

